



























import { Component, Vue, Prop, Watch } from 'vue-property-decorator'

@Component({
  components: {

  },
  filters: {
    fenToYuan (num: any) {
      if (typeof num !== 'number' || isNaN(num)) return null
      return '¥' + (num / 100).toFixed(2)
    }
  }
})
export default class payAdmin extends Vue {
  $refs : any
  accountId = ''
  floorList = []
  data = [
    [],
    [],
    []
  ]
  selectedIndex = []
  activated () {
    this.pageInit()
  }
  pageInit () {
    let query = this.$route.query
    if (query.accountId) {
      this.accountId = query.accountId
      this.getFloorByAccountId()
    }
  }
  getFloorByAccountId () {
    const toast = this['$createToast']({
      time: 0,
      txt: '数据获取中...'
    })
    toast.show()
    this.$store.dispatch('getFloorByAccountId', { accountId: this.accountId }).then((response) => {
      if (response.success) {
        let resData = response.data
        this.floorList = response.data.list
      }
      toast.hide()
    })
  }

  toOtherPage (type: any) {
    if (type) {
      let path = '/' + type
      this.$router.push({ path: path, query: { accountId: this.accountId} })
    }
  }

  floorBoxClick (item: any) {
    console.log(item)
    console.log(this.$refs)
    this.$refs.drawer.show()
  }

  changeHandler (index, item, selectedVal, selectedIndex, selectedText) {
    console.log('change====')
    setTimeout(() => {
      this.$refs.drawer.refill(index + 1, this.floorList)
    }, 200)
  }
}
